import React from "react";
import Toggle from "../Toggle/Toggle";
import "./Navbar.css";

import { themeContext } from "../../context/context";
import { useContext } from "react";
import {Link} from 'react-scroll';


function Navbar() {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  return (
    <div className="n-wrapper">
      <div className="n-left">
        <div className="n-logo">Muzammil Ishtiaq</div>
        <span><Toggle/></span>
      </div>
      <div className="n-right">
        <div className="n-list">
          <ul >
            <Link  to='navbar' spy smooth  activeClass="activeClass"><li style={{color: darkMode?'white':''}}>Home</li></Link>
            <Link  to='Serivces'spy smooth ><li style={{color: darkMode?'white':''}}>Serivces</li></Link>
            <Link  to='Experience'spy smooth ><li style={{color: darkMode?'white':''}}>Experience</li></Link>
            <Link  to='Portfolio' spy smooth><li style={{color: darkMode?'white':''}}>Portfolio</li></Link>
            <Link  to='Testimonial'spy smooth ><li style={{color: darkMode?'white':''}}>Testimonial</li></Link>
          </ul>
        </div>
       <Link  to='Contact' spy  smooth> <button className="button n-button">Contact</button></Link>
      </div>
    </div>
  );
}

export default Navbar;
