import React from 'react'
import './Intro.css';
import FloatingDiv from '../FloatingDiv/FloatingDiv';

import Linkedin from '../../img/linkedin.png';
import instagram from '../../img/instagram.png';
import github from '../../img/github.png';

import Vector1 from '../../img/Vector1.png';
import Vector2 from '../../img/Vector2.png';
import boy from '../../img/boy.png';
import thumbup from '../../img/thumbup.png';
import crown from '../../img/crown.png';
import glassesimoji from '../../img/glassesimoji.png';
import { themeContext } from "../../context/context";
import { useContext } from "react";
import { motion } from "framer-motion"
function Intros() {
  const transition = {duration:2, type:'spring'}
  const theme = useContext(themeContext);
const darkMode = theme.state.darkMode;
  return (
<div className="intros">
  <div className="in-left">                    
  <div className="in-name">
    <span style={{color: darkMode?'white':''}}>Hy! I Am </span>
    <span>Andrew Thomas</span>
    <span>Frontend Developer with high level of experience in web designing and development, producting the Quality Work</span>
  </div>
<button className="button i-button">Hire Me</button>
<div className="in-socal-icon">
<a href=""><img src={github} alt="" /></a>
  <a href=""><img src={instagram} alt="" /></a>
  <a href=""><img src={Linkedin} alt="" /></a>
</div>
  </div>
  <div className="in-right">
  <img src={Vector1} alt="" />
  <img src={Vector2} alt="" />
  <img src={boy} alt="" />
  <motion.img
  initial={{left:'-36%'}}
  whileInView={{left:'-24%'}}
  src={glassesimoji}
  transition={transition}
  alt="" />

  <motion.div
  initial={{top:'4%', left:'74%'}}
  whileInView={{left:'68%'}}
  transition={transition}
  style={{position:'absolute', top:'-1rem', left:'18rem', zIndex:'10'}}>
    <FloatingDiv Image={crown} txt1={"Web"} txt2={"Developer"} className="Floatingdiv" />
  </motion.div>
  <motion.div
  initial={{left:'8rem', top:'14rem'}}
  whileInView={{left:'0rem'}}
  transition={transition}
  
  style={{position:'absolute', top:'15rem', left:'-2rem', zIndex:'10'}}>
    <FloatingDiv Image={thumbup} txt1={"Best Design"} txt2={"Award"} className="Floatingdiv" />
  </motion.div>
  {/* blur div */}
  <div className="blur" style={{backgroundColor:"rgb(238 210 255 )"}}>
    <div className="blur" style={{background:"#C1F5FF", top:"17rem", width:"21rem", height:"11rem", left:"-9rem"}}></div>
  </div>
  </div>
 
 
  
</div>
  )
}

export default Intros;
