import React from "react";
import "./Service.css";
import Card from "../Card/Card";
import Resume from  './resume.pdf'
import { motion } from "framer-motion";

import heartemoji from "../../img/heartemoji.png";
import glasses from "../../img/glasses.png";
import humble from "../../img/humble.png";
import { themeContext } from "../../context/context";
import { useContext } from "react";
function Service() {
  const transition = {duration:1, type:'spring'}
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;
  return (
    <div className="service" id="Serivces">
      <div className="awsome-left">
        <span style={{color: darkMode?'white':''}}>My Awsome</span>
        <span>Service</span>
        <span>
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Quasi facere
          nam officia distinctio voluptatum corporis
          <br />
          eaque ex magni ipsa.Nam enim praesentium veritatis maiores quae
          laboriosam esse similique libero cumque!
        </span>
        <a href={Resume} download={Resume}><button className="button s-button">Download CV</button></a>
        <div
          className="blur blur-s"
          style={{ backgroundColor: "#ABF1FF94" }}
        ></div>
      </div>
      <div className="cards-right ">
        <motion.div
        whileInView={{left:'14rem'}}
        initial={{left:'25%'}}
        transition={transition}

        className="card" style={{ position: "absolute", left: "14rem" }}>
          <Card
          className="cards"
            image={heartemoji}
            heading={"Design"}
            para={"Figma Sketch Photoshop, Adobe Illustrator. Adobe XD"}
          />
        </motion.div>
        <div className="card" style={{ position: "absolute", left: "2rem",top:"10rem" }}>
          <Card
          className="cards"
            image={glasses}
            heading={"Developer"}
            para={"Html, Css Javascript, React"}
          />
        </div>
        <motion.div
         whileInView={{left:'14rem'}}
         initial={{left:'25%'}}
         transition={transition}
        className="card" style={{ position: "absolute", left: "14rem", top:"17rem" }}>
          <Card
          className="cards"
            image={humble}
            heading={"UI/UX"}
            para={"Figma Sketch Photoshop, Adobe Illustrator. Adobe XD"}
          />
        </motion.div>
        <div className="blur s-blur" style={{background:"var(--purple)"}}></div>
        
      </div>
    </div>
  );
}

export default Service;
